.nav-links-components {
  padding: 12px 180px;
  background: #e7d3f2;
  color: white;
  list-style: none;
  display: flex;
  justify-content: space-around;
  margin: 0;
}
.nav-logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 18px 60px;
  width: 10px;
}

.nav-logo>img {
  width: 200px;
  height: auto;
}
