.searchInputs {
  margin-top: 50px;
  display: flex;
}
/*> means only direct children*/
.search>p{
  font-size: 35px;
  margin-bottom: 10px;
}
.search input {
  background-color: white;
  border: 0;
  border-radius: 2px 0 0 2px;
  font-size: 18px;
  padding: 15px;
  height: 60px;
  width: 300px;
}

.searchIcon {
  height: 60px;
  width: 50px;
  background-color: rgba(239, 234, 234, 0.62);
  display: grid;
  place-items: center;
}

input:focus {
  outline: none;
}
.searchIcon svg {
  font-size: 35px;
}

.dataResult {
  margin-top: 5px;
  width: 300px;
  height: 200px;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  overflow: hidden;
  overflow-y: auto;
}

.dataResult::-webkit-scrollbar {
  display: none;
}

.dataResult .dataItem {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  color: black;
}

.dataItem p {
  margin-left: 10px;
}
a {
  text-decoration: none;
}

a:hover {
  background-color: lightgrey;
}

#clearBtn {
  cursor: pointer;
}

.selectedItems {
  position: relative;
}
.selectedItems li{
  list-style-type: none;
  margin: 5px;
  color: #ece7ee;
}
.selectedItems p{
  color: #eeeeee;
  font-size: 25px;
}

.elementButton {
  position: absolute;
  right: 0;
  width: 50px;
  align-items: center;
  border: #e7d3f2;
  color: #f2eded;
  background: rgba(239, 234, 234, 0.62);
  border-radius: 12px;
}
